async function routerBuyUsdt(amountEth) {
  const amountWei = eth2wei(amountEth);
  const router = ROUTER;
  const addressWETH = await router.WETH();

  let path = [
    addressWETH,
    USDT_ADDRESS
  ];

  const amountOutMin = '1';
  const myAddress = await SIGNER.getAddress();
  const deadline = moment().unix() + 300;

  const options = { value: amountWei };
  const swapResult = await router.swapExactETHForTokens(amountOutMin, path, myAddress, deadline, options);

  log.debug('routerBuyUsdt tx: ' + swapResult.hash);
  await swapResult.wait();
  return swapResult.hash;
}

async function routerBuyToken(amountEth) {
  const amountWei = eth2wei(amountEth);
  const router = ROUTER;

  let path = [
    USDT_ADDRESS,
    TOKEN_ADDRESS
  ];

  const amountOutMin = '1';
  const myAddress = await SIGNER.getAddress();
  const deadline = moment().unix() + 300;

  const swapResult = await router.swapExactTokensForTokens(
    amountWei,
    amountOutMin, // minimum
    path,
    myAddress,
    deadline
  );

  log.debug('routerBuyToken tx: ' + swapResult.hash);
  await swapResult.wait();
  return swapResult.hash;
}

async function nftData() {
  const ADDRESS_POSPOOL = await NFT.ADDRESS_POSPOOL();
  const ADDRESS_REVRECEIVER = await NFT.ADDRESS_REVRECEIVER();
  const ADDRESS_TOKEN = await NFT.ADDRESS_TOKEN();
  const lastUpdateTime = await NFT.lastUpdateTime();
  const nextUpdateTime = lastUpdateTime.toNumber() + (3600 * 24);
  const session = await NFT.session();
  const rewardPerNFT = await NFT.rewardPerNFT();
  const stat = await NFT.stat();

  const dataListLength = stat[0].toNumber();
  const wrListLength = stat[1].toNumber();

  const dataList = [];
  for (let i = 1; i < dataListLength; i++) {
    const row = await NFT.dataListAtIndex(i);
    dataList.push({
      id: row[0].toNumber(),
      createdTime: row[1].toNumber(),
      allowBurnTime: row[2].toNumber(),
      session: row[3].toNumber()
    });
  }

  // const wrList = [];
  // for (let i = 0; i < wrListLength; i++) {
  //   const row = await NFT.withdrawListAtIndex(i);
  //   wrList.push({
  //     claimer: row[0],
  //     withdrawAmount: wei2eth(row[1]),
  //     deadline: row[2].toNumber(),
  //     done: row[3]
  //   });
  // }

  const userOwnedNFTIds = [];
  const userAddress = await SIGNER.getAddress();
  const num = await NFT.balanceOf(userAddress);
  for (let i = 0; i < num.toNumber(); i++) {
    const nftId = await NFT.tokenOfOwnerByIndex(userAddress, i);
    userOwnedNFTIds.push(nftId.toNumber());
  }

  const balanceToken = await TOKEN.balanceOf(NFT_ADDRESS);
  const balanceUSDT = await USDT.balanceOf(NFT_ADDRESS);
  const balanceETH = await PROVIDER.getBalance(NFT_ADDRESS);

  let revenue = 0;
  let mayUpdate = false;
  let mayWithdraw = false;
  let newSession;
  try {
    const ret = await NFT.callStatic.update();
    revenue = ret[0];
    newSession = ret[1].toNumber();
    mayUpdate = newSession > session.toNumber();
    log.debug('** nft update static success **');
    console.log({ revenue, newSession });
  } catch (err) {
    log.debug('** nft update error **');
    log.error(err);
  }

  let pendingWithdraw = false;
  let pendingWithdrawIndex;
  let pendingWithdrawDeadline;

  for (let i = 0; i < wrListLength; i++) {
    const ii = wrListLength - i - 1;
    const row = await NFT.withdrawListAtIndex(i);
    const claimer = row[0];
    const deadline = row[2].toNumber();
    const done = row[3];
    if (claimer.toUpperCase() === userAddress.toUpperCase() && !done) {
      pendingWithdraw = true;
      pendingWithdrawIndex = ii;
      pendingWithdrawDeadline = deadline;

      const block = await PROVIDER.getBlock();
      const ts = block.timestamp;
      mayWithdraw = ts > deadline;
      break;
    }
  }

  const numNFT = await NFT.totalSupply();
  const ui = await IPP.userInterest(NFT_ADDRESS);

  const ret = {
    revenue: wei2eth(ui),
    totalNFT: numNFT.toNumber(),
    ADDRESS_POSPOOL,
    ADDRESS_REVRECEIVER,
    ADDRESS_TOKEN,
    lastUpdateTime: lastUpdateTime.toNumber(),
    nextUpdateTime,
    session: session.toNumber(),
    newSession,
    rewardPerNFT: wei2eth(rewardPerNFT),
    dataList,
    // wrList,
    userOwnedNFTIds,
    balanceToken: wei2eth(balanceToken),
    balanceETH: wei2eth(balanceETH),
    balanceUSDT: wei2eth(balanceUSDT),
    revenue: wei2eth(revenue),
    mayUpdate,
    mayWithdraw,
    pendingWithdraw,
    pendingWithdrawIndex,
    pendingWithdrawDeadline
  }

  log.debug('** nftData **');
  log.debug(ret);

  await checkPool();
  return ret;
}

