async function treasuryData() {

  const ADDRESS_COIN = await TREASURY.ADDRESS_COIN();
  const ADDRESS_TOKEN = await TREASURY.ADDRESS_TOKEN();
  const ADDRESS_NFT = await TREASURY.ADDRESS_NFT();
  const START_PRICE = await TREASURY.START_PRICE();
  const lastUpdateTime = await TREASURY.lastUpdateTime();
  const basePrice = await TREASURY.getBasePrice();

  const userAddress = await SIGNER.getAddress();
  const otherAddress = TREASURY_ADDRESS;
  const ownerAddress = await TREASURY.owner();

  const b = await TREASURY.coinToToken(eth2wei('1'), otherAddress);
  const a = BigNumber.from(eth2wei('1'));
  let buyPrice = a.mul(eth2wei('1')).div(b);
  let sellPrice = await TREASURY.tokenToCoin(eth2wei('1'), TREASURY_ADDRESS);
  const price = await TREASURY.tokenToCoin(eth2wei('1'), ownerAddress);
  const balanceToken = await TOKEN.balanceOf(TREASURY_ADDRESS);
  const balanceUSDT = await USDT.balanceOf(TREASURY_ADDRESS);
  const balanceETH = await PROVIDER.getBalance(TREASURY_ADDRESS);

  const userUSDTAllowance = await USDT.allowance(userAddress, TREASURY_ADDRESS);
  const userTokenAllowance = await TOKEN.allowance(userAddress, TREASURY_ADDRESS);

  const block = await PROVIDER.getBlock();
  const ts = block.timestamp;

  const mayUpdate = balanceUSDT.gte(1 + ZERO18) && (ts > lastUpdateTime.toNumber());

  let swapBuyPrice = '0';
  let swapSellPrice = '0';

  try {
    const a = await swapToken2USDT('1');
    swapSellPrice = wei2eth(a);
  } catch (err) {
    log.error(err);
  }

  try {
    const amountUSDT = '0.01';
    const b = await swapUSDT2Token(amountUSDT);
    const a = BigNumber.from(eth2wei(amountUSDT) + ZERO18);
    const c = a.div(b);
    swapBuyPrice = wei2eth(c);
  } catch (err) {
    log.error(err);
  }

  buyPrice = wei2eth(buyPrice);
  sellPrice = wei2eth(sellPrice);
  let enableArbitrage = false;

  let doArbitrage = false;
  if (Number(swapBuyPrice) < Number(sellPrice)) {
    enableArbitrage = true;
  }

  if (Number(buyPrice) < Number(swapSellPrice)) {
    enableArbitrage = true;
  }

  // bcv here
  const bp = await TREASURY.getBasePrice();
  const pdArr = await BCV.getPriceData();

  log.debug('** bcv data **');
  log.debug({
    bp1: bp.toString(),
    bp2: pdArr[0].toString(),
    cp2: pdArr[1].toString(),
    mp2: pdArr[2].toString()
  });


  const ret = {
    ADDRESS: TREASURY_ADDRESS,
    ADDRESS_COIN,
    ADDRESS_TOKEN,
    ADDRESS_NFT,
    START_PRICE: START_PRICE.toString(),
    buyPrice: buyPrice,
    sellPrice: sellPrice,
    swapBuyPrice,
    swapSellPrice,
    enableArbitrage,
    price: wei2eth(price),
    lastUpdateTime: lastUpdateTime.toNumber(),
    basePrice: wei2eth(basePrice),
    balanceToken: wei2eth(balanceToken),
    balanceETH: wei2eth(balanceETH),
    balanceUSDT: wei2eth(balanceUSDT),
    userUSDTAllowance: userUSDTAllowance.toString(),
    userTokenAllowance: userTokenAllowance.toString(),
    mayUpdate
  }

  log.debug(ret);
  return ret;
}

