  setAmountByPctg(pctg, swapType) {
    const userData = this.state.userData;
    if (swapType === 'buy') {
      let numToken = Number(userData.userUSDT) * pctg / 100;
      if (pctg === 100) numToken = userData.userUSDT;
      this.setAmountInput(numToken + '', swapType);
    }
    if (swapType === 'sell') {
      let numToken = Number(userData.userToken) * pctg / 100;
      if (pctg === 100) numToken = userData.userToken;
      this.setAmountInput(numToken + '', swapType);
    }
  }

  async setAmountInput(amount, swapType) {
    this.setState({ amountInput: amount });
    if (swapType === 'buy') {
      let resultSwap = 0;
      if (Number(amount > 0)) resultSwap = await evmLib.coinToToken(amount);
      this.setState({ resultSwap });
    } else if (swapType === 'sell') {
      let resultSwap = 0;
      if (Number(amount > 0)) resultSwap = await evmLib.tokenToCoin(amount);
      this.setState({ resultSwap });
    }
  }

