  renderPanelLeave() {
    if (!this.state.connected) return null;
    const txt1 = 'Burn NFT and unstake 1000 CFX to leave.';
    let desc = (
      <View>
        <Text style={{ textAlign: 'center' }}>{txt1}</Text>
        <Text style={{ textAlign: 'center', color: 'red' }}>[cult access]</Text>
      </View >
    );

    const nftData = this.state.nftData;
    if (nftData.pendingWithdraw) {
      if (nftData.mayWithdraw) {
        const disable = false;
        desc = (
          <View>
            <Text style={{ textAlign: 'center' }}>{txt1}</Text>
            <Text style={{ textAlign: 'center', color: 'green' }}>[1000 CFX can be unstaked]</Text>
          </View >
        );
        return this.renderPanel(TITLE['burn'], desc, 'Unstake CFX', () => this.nftWithdraw(nftData.pendingWithdrawIndex), disable, 'sign-out');
      } else {
        const disable = true;
        const deadlineStr = moment.unix(nftData.pendingWithdrawDeadline).format('DD MMM HH:mm');
        desc = (
          <View>
            <Text style={{ textAlign: 'center' }}>{txt1}</Text>
            <Text style={{ textAlign: 'center', color: 'red' }}>[unstake enable on {deadlineStr}]</Text>
          </View >
        );
        return this.renderPanel(TITLE['burn'], desc, 'Unstake CFX', () => this.nftWithdraw(nftData.pendingWithdrawIndex), disable, 'sign-out');
      }
    }

    if (this.state.selectedNFT && this.state.selectedNFT > 0) {
      desc = (
        <View>
          <Text style={{ textAlign: 'center' }}>{txt1}</Text>
        </View >
      );
    }

    let disable = true;
    const userData = this.state.userData;

    if (this.state.selectedNFT && this.state.selectedNFT > 0) {
      const dataListByKey = _.keyBy(nftData.dataList, 'id');
      const selectedNFTData = dataListByKey[this.state.selectedNFT];
      const allowBurnTime = selectedNFTData.allowBurnTime;
      const nowts = userData.blocktime;
      if (nowts > allowBurnTime) {
        disable = false;
      } else {
        const msg = '[burn after ' + moment.unix(allowBurnTime).format('DD MMM HH:mm') + ']';
        desc = (
          <View>
            <Text style={{ textAlign: 'center' }}>{txt1}</Text>
            <Text style={{ textAlign: 'center', color: 'red' }}>{msg}</Text>
          </View >
        );
      }
    }


    return this.renderPanel(TITLE['burn'], desc, 'Burn NFT', () => this.burnNFT(this.state.selectedNFT), disable, 'sign-out');
  }

