  renderPanelSwap(swapType) {
    const userData = this.state.userData;
    const treasuryData = this.state.treasuryData;
    const userTokenAllowance = treasuryData.userTokenAllowance && treasuryData.userTokenAllowance !== '0';
    const userUSDTAllowance = treasuryData.userUSDTAllowance && treasuryData.userUSDTAllowance !== '0';
    let needApprove = true;
    let resultSwap = this.state.resultSwap ? this.state.resultSwap : '0';
    let token1;
    let token2;
    let approveFunc;
    let swapFunc;
    let ownedAmount;

    if (swapType === 'buy') {
      if (userUSDTAllowance) needApprove = false;
      approveFunc = () => this.approveUSDT();
      swapFunc = () => this.swapCoinToToken(this.state.amountInput, this.state.selectedNFT);
      token1 = 'USDT';
      token2 = 'XYZ';
      ownedAmount = userData.userUSDT;
    }
    else if (swapType === 'sell') {
      if (userTokenAllowance) needApprove = false;
      approveFunc = () => this.approveToken();
      swapFunc = () => this.swapTokenToCoin(this.state.amountInput, this.state.selectedNFT);
      token1 = 'XYZ';
      token2 = 'USDT';
      ownedAmount = userData.userToken;
    }

    const s1 = { textAlign: 'center', fontWeight: 'bold', textTransform: 'uppercase' };

    let enableSwap = false;
    if (this.state.selectedNFT && this.state.selectedNFT > 0) {
      if (!needApprove && Number(this.state.amountInput) > 0 && Number(ownedAmount) > 0) {
        enableSwap = true;
      }
    }


    return (
      <View style={{ flex: 1, alignItems: 'center', justifyContent: 'center', backgroundColor: 'white', padding: vp * 2 }}>
        <View style={{ borderWidth: 1, borderColor: 'gainsboro', backgroundColor: 'white', padding: vp * 2 }}>
          <Text> </Text>
          <Text style={s1}>Swap {token1} to {token2}</Text>
          <Text> </Text>
          <Text> </Text>
          <Text>Owned {ownedAmount} {token1}</Text>
          <Text> </Text>
          <View style={{ flexDirection: 'row' }}>
            <View style={{ flex: 1 }}>
              <TextInput
                value={this.state.amountInput}
                onChangeText={txt => this.setAmountInput(txt, swapType)}
                keyboardType='numeric'
                placeholder={'Enter ' + token1}
                style={{ borderWidth: 1, padding: 5, flex: 1 }}
              />
            </View>
            <View style={{ width: vp }} />
            <View style={{ flexDirection: 'row' }}>
              <View style={{ flex: 1 }}>
                <Button style={{ flex: 1 }} type='outline' title='50%' onPress={
                  () => this.setAmountByPctg(50, swapType)
                } />
              </View>
              <View style={{ width: vp }} />
              <View style={{ flex: 1 }}>
                <Button style={{ flex: 1 }} type='outline' title='100%' onPress={
                  () => this.setAmountByPctg(100, swapType)
                } />
              </View>
            </View>
          </View>
          <Text> </Text>
          <Text>Receive {resultSwap} {token2}</Text>
          <Text> </Text>
          <Button disabled={!needApprove} title={'Approve ' + token1} onPress={
            () => approveFunc()} />
          <View style={{ height: vp }} />
          <Button disabled={!enableSwap} title={'Swap ' + token1} onPress={
            () => swapFunc()} />
          <View style={{ height: vp }} />
          <Button disabled={false} type='outline' title='Back' onPress={
            () => this.setState({ show: 'content' })} />
        </View>
      </View>
    );
  }

